section#single-product-item {
  header {
    height: 500px;
    width: 100%;
    position: relative;
    overflow: hidden;
    &::after {
      content: "";
      background: rgba(0, 0, 0, 0.5);
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      position: absolute;
      z-index: -1;
      display: block;
    }
    img {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      min-width: 100%;
      height: 100%;
      z-index: -1;
    }
    .single-product-item__heading {
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: flex-end;
      height: 500px;
      z-index: 3;
      h1 {
        background: #041e42;
        padding: 1rem 2rem;
        color: #fff;
        font-weight: 600;
        font-size: 25px;
        margin: 0;
      }
    }
  }
  main {
    article.single-product-item__content {
      padding: 5rem 0;
      h2 {
        font-size: 25px;
        font-weight: 600;
        margin-bottom: 50px;
        text-transform: uppercase;
        @media all and (max-width: 576px) {
          text-align: center;
        }
      }
      .single-product-item__content__item {
        cursor: pointer;
        margin-bottom: 40px;
        min-height: 240px;
        overflow: hidden;
        .single-product-item__content__item-content-wrap {
          min-height: 240px;
          display: flex;
          align-items: center;
          flex-direction: column;
          justify-content: center;
          background: #041e42;
          h3 {
            font-weight: 600;
            margin-bottom: 40px;
            font-size: 18px;
            color: #fff;
            text-transform: capitalize;
          }
        }
        .single-product-item__content__item__image {
          height: 60px;
          width: 100%;
          position: relative;
          overflow: hidden;
          img {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            height: 100%;
            width: auto;
          }
        }
      }
    }
  }
}
