.Posts {
  display: flex;
  flex: 9;
  flex-wrap: wrap;
  //   margin: 10px;
}

.post {
  width: 390px;
  margin-top: 50px;
  margin-left: 50px;
  margin-right: 25px;
  margin-bottom: 40px;
}
.postImage {
  width: 100%;
  height: 250px;
  object-fit: cover;
  border-radius: 7px;
}
.postInfo {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.postCat {
  font-family: "Josefin Sans", sans-serif;
  color: goldenrod;
  font-size: 11px;
  line-height: 20px;
  margin-top: 15px;
  margin-right: 10px;
  cursor: pointer;
}
.postTitle {
  font-family: "Josefin Sans", sans-serif;
  font-size: 17px;
  font-weight: 700;
  margin-top: 15px;
  cursor: pointer;
}
.postDate {
  font-family: "Lora", serif;
  font-style: italic;
  font-size: 15px;
  color: rgba(153, 153, 153, 0.938);
}
.postDesc {
  font-family: "Varela Round", sens-serif;
  font-size: 14px;
  color: #444;
  line-height: 24px;
  margin-top: 15px;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}
