.test {
  background-image: url(https://ake.com.tr/uploads/images/fabrika_dis_cephe_slider.jpg);
  background-position: 50% -97px;
  padding: 0;
  margin: 0;
  height: 500px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  justify-content: center;
  padding: 0 0 0 110px;

  .work__contact {
    position: relative;
    border: 10px solid white;
    width: 200px;
    padding: 70px 0px 70px 30px;
    border-right: 0;
    .work-item {
      h2 {
        color: #fff;
        font-size: 22px;
      }
    }
    &::before {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      height: 22%;
      width: 10px;
      background-color: white;
    }
    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      right: 0;
      height: 22%;
      width: 10px;
      background-color: white;
    }
  }
  .work-item2 {
    color: white;
    .link {
      color: #fff;
      transition: 0.5s;
    }
    .link:hover {
      color: #000 !important;
    }
  }
}
.container {
  .contact-title {
    width: 100%;
    height: 83px;
    position: relative;
    font-size: 36px;
    line-height: 42px;
    font-weight: 800;
  }
  .contact-title::before {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 80px;
    height: 3px;
    background-color: black;
  }
  .contact-list {
    padding-top: 40px;
    .icon {
      width: 15px;
      height: 19px;
    }
  }
}
.contact-list {
  li {
    font-size: 1.3rem;
    margin-bottom: 16px;
    font-weight: 600;
  }
  li a {
    font-size: 17px;
    color: #000;
    font-weight: 600;
    margin-left: 10px;
    li svg {
      font-size: 40px;
    }
  }
}
.list {
  span {
    font-size: 14px;
    font-weight: 900;
  }
  li {
    font-size: 15px;
  }
  li strong {
    font-weight: 800;
  }
}
.list {
  ul {
    li {
      list-style-type: disc;
    }
  }
}
.icon {
  width: 25px !important;
  height: 25px;
}
.contact-form {
  padding-top: 40px;
  .contact-1 {
    border: 6px solid #000;
    padding: 40px;
    label {
      font-weight: 600;
      margin-bottom: 10px;
    }
    .form-control {
      border: 0px;
      border-bottom: 1px solid #000;
      box-shadow: none !important;
    }
    .form-title {
      margin-bottom: 25px;
    }
  }
}
.link-icon :hover {
  background: rgb(29, 29, 29);
  transition: all 0.2s ease;
  transform: scale(1.1);
  color: #fff;
}
.batr {
  padding-top: 80px;
  button {
    width: 120px;
    height: 40px;
    background: #000;
    color: #fff;
    border: none;
    text-align: center;
    font-size: 12px;
    line-height: 18px;
  }

  .map {
    width: 100%;
    z-index: 1000;
  }
}
@media screen and (max-width: 792px) {
  .test {
    padding: 0 0 0 50px;
  }
  .contact-form {
    .contact-1 {
      padding: 15px 10px;
    }
  }
  .row {
    .contact-title {
      font-size: 30px;
      line-height: 35px;
    }
  }
}
@media screen and (max-width: 475px) {
  .link-icon {
    .iconInformation {
      margin-top: 10px !important;
    }
  }
  .row {
    .contact-title {
      font-size: 20px;
      line-height: 25px;
    }
  }
}
