section#products {
  header {
    position: relative;
    height: 600px;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
    }
    h1 {
      color: white;
      z-index: 2;
      font-family: "Griffo M";
      font-size: 4rem;
      font-weight: 700;
    }
    &::after {
      content: "";
      background: rgba(0, 0, 0, 0.4);
      height: 100%;
      width: 100%;
      position: absolute;
    }
    &::before {
      content: "";
      bottom: 0;
      left: 50%;
      transform: translateY(50%);
      width: 1px;
      height: 120px;
      position: absolute;
      background: white;
      z-index: 2;
    }
  }
  article.main-part {
    background-color: #eceae8;
    .main-info {
      color: #041e42;
      padding: 75px 0;
      margin-bottom: 120px;
      h2 {
        font-size: 3.125rem;
        font-family: "Griffo M";
        font-weight: 400;
        margin-bottom: 50px;
        position: relative;
        &::after {
          position: absolute;
          bottom: -20px;
          left: 50%;
          transform: translateX(-50%);
          background: #cba052;
          content: "";
          z-index: 1;
          width: 80px;
          height: 3px;
        }
      }
      p {
        font-size: 1.3rem;
        line-height: 1.5;
        &:last-of-type {
          margin-bottom: 3rem;
        }
      }
      a {
        padding: 10px 20px;
        background: #041e42;
        color: white;
        text-transform: uppercase;
        border: 2px solid transparent;
        font-size: 0.875rem;
        &:hover {
          color: #041e42;
          background: transparent;
          border: 2px solid #041e42;
        }
      }
    }
    .main-products {
      margin-bottom: 50px;
      color: #041e42;
      h4 {
        font-family: "Griffo M";
        font-size: 1.875rem;
        text-transform: capitalize;
        position: relative;
        margin-bottom: 5rem;
        &::after {
          position: absolute;
          bottom: -20px;
          left: 50%;
          transform: translateX(-50%);
          background: #cba052;
          content: "";
          z-index: 1;
          width: 80px;
          height: 3px;
        }
      }
      .productItem__imageBox {
        position: relative;
        width: 100%;
        height: 210px;
        overflow: hidden;
        &:hover {
          img {
            transform: scale(1.1);
          }
        }
        img {
          width: 100%;
          height: auto;
        }
      }
      .productItem__info {
        background-color: white;
        padding: 15px;
        h6 {
          text-align: left;
          font-family: "Griffo M";
          padding: 5px 0 40px;
          font-size: 1.75rem;
        }
        a {
          width: 100%;
          background: #041e42;
          color: white;
          text-transform: uppercase;
          font-size: 0.875rem;
          padding: 10px 0;
          letter-spacing: 1px;
          display: block;
          border: 2px solid transparent;
          &:hover {
            border: 2px solid #041e42;
            background: transparent;
            color: #041e42;
          }
        }
      }
    }
  }
  article.additional-part {
    background: white;
    padding: 50px 0 100px;
    .resourceImage {
      background: #f76786;
      position: relative;
      height: 280px;
      width: 100%;
      display: flex;
      img {
        width: 100%;
        min-height: 100%;
        align-items: center;
        justify-content: center;
      }
      &:hover {
        img {
          transform: translate(10px, -10px);
        }
      }
      @media (min-width: 768px) and (max-width: 992px) {
        height: 210px;
      }
    }
    a {
      display: block;
      padding: 25px 0 15px;
      border-bottom: 1px solid #041e42;
      font-family: "Griffo M";
      text-transform: capitalize;
      font-size: 1.875rem;
      color: #041e42;
    }
  }
}
