* {
  margin: 0;
  padding: 0;
}

.news-section-1 {
  background-image: url("../img/Press-Release-us-bg_blank_updated.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  width: 100%;
  height: 600px;
  align-items: center;
  justify-content: center;
  text-align: center;
  position: relative;
  z-index: 1;

  .news-section-1-text {
    position: absolute;
    left: 10%;
    width: 80%;
    top: 27%;
    color: #fff;
    h1 {
      font-weight: 700;
      font-family: grifo_mmedium, "Times New Roman", serif;
      font-size: 80px;
    }
    .new-section-1-btn {
      background-color: #041e42;
      border: 3px solid #fff;
      color: #fff;
      height: 50px;
      width: 240px;
      margin-top: 40px;
    }
    .new-section-1-btn:hover {
      transform: translateY(-3px);
      box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
      background-color: #fff;
      color: #041e42;
      border: 3px solid #041e42;
    }
  }
}
@media screen and (max-width: 900px) {
  .news-section-1 {
    display: flex;
    .news-section-1-text h1 {
      font-size: 30px;
    }
  }
}
.news-section-2 {
  justify-content: center;
  align-items: center;
  height: auto;
  height: auto;
  text-align: center;
  padding-top: 120px;
  padding-left: 50px;
  padding-bottom: 50px;

  padding-right: 50px;
  background-color: rgb(238, 238, 238);

  .news-section-2-text {
    justify-content: center;
    left: 38%;
    color: #041e42;
    text-align: center;
    position: absolute;
    align-items: center;

    .news-section-2-h1 {
      font-size: 38px;
      font-weight: 600;
      font-family: grifo_mmedium, "Times New Roman", serif;
    }

    .news-section-2-line {
      border-top: 4px solid rgb(182, 119, 1);
      left: 40%;
      position: absolute;
      background-color: saddlebrown;
      width: 50px;
      margin-top: 7%;
    }
    .news-section-2-p {
      margin-top: 30%;
      font-size: 20px;
    }
  }
  .news-card-group {
    margin-top: 15%;
    justify-content: space-between !important;
  }
  .news-section-2-row {
    margin-top: 20%;
    justify-content: space-between !important;
    flex-basis: 30%;
    display: flex;
    justify-items: space-between;
  }
  .news-section-2-col {
    flex-basis: 30%;
    border: 1px solid black;
    background-size: cover;
    overflow: hidden;
    background-position: center;
    position: relative;
    transition: 0.3s;
  }

  .Card-Img:hover {
    cursor: pointer;
    transform: scale(1.1);
    opacity: 1;
  }
  .Card-body {
    background-color: #ffff;
  }
  .Card-footer {
    background-color: #fff;
  }
  .nnn {
    border: 3px solid rgb(214, 0, 0);
    background-color: salmon;
  }
  @media screen and (max-width: 900px) {
    .news-section-2-text {
      margin-left: -100px;
      margin-top: -100px;

      font-size: 14px;
      .news-section-2-h1 {
        font-size: 30px;
      }
      .news-section-2-p {
        margin-top: 18%;
      }
    }
    .news-section-2-row {
      flex-direction: column;
      .news-section-2-col {
        margin-top: 5%;
      }
    }
  }
}

.news-section-3 {
  height: 400px;
  margin-top: 10%;
  .news-section_3_text {
    justify-content: center;
    align-items: center;
    color: #041e42;
    text-align: center;
    margin-top: 10%;
    align-items: center;
    .news-section-3-p {
      color: rgb(194, 126, 0);
      font-size: 20px;
    }
    .news-section-3-line {
      border-top: 4px solid rgb(182, 119, 1);
      left: 48%;
      position: absolute;
      background-color: saddlebrown;
      width: 50px;
      margin-top: 2%;
    }

    .news-section-3-h1 {
      margin-top: 50px;
      font-size: 38px;
      font-weight: 600;
      font-family: grifo_mmedium, "Times New Roman", serif;
    }
  }
}
.Carousel-page {
  height: 700px;

  .carousel-item {
    width: 100%;
    height: 600px;
    position: relative;
    img {
      width: 100%;
      height: 600px;
      filter: brightness(50%);
    }
  }
}
.News_LastPage_Imgg {
  img {
    max-width: 100%;
    max-height: 100%;
  }
  width: 90%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 30px;
  margin: 100px auto;
  width: 80%;
  .Section_4-container {
    .Section_4-container-inside {
      cursor: pointer;
      &:hover img {
        transform: translate(2%, -2%);
      }
      h3 {
        margin: 20px 0;
        color: #000;
      }
      .card-img {
        background-color: #f65275;
        img {
          transition: 0.3s;
        }
      }
    }
    .Section_4-container-line {
      margin-top: 50px;
      width: 100%;
      height: 4px;
      background-color: #041e5f;
    }
  }
}
.ForMedia h1 {
  font-family: grifo_mmedium, "Times New Roman", serif;
  font-size: 50px;
  font-weight: 400;
  color: #041e42;
  line-height: 50px;
}
@media (max-width: 900px) {
  .News_LastPage_Imgg {
    grid-template-columns: repeat(1, 1fr);
  }
}
