section#usageAreas {
  header {
    position: relative;
    height: 600px;
    display: flex;
    justify-content: center;
    align-items: center;

    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 400px;
      height: 100%;
    }
    h1 {
      color: white;
      z-index: 2;
      font-family: "Griffo M";
      font-size: 4rem;
      font-weight: 700;
    }
    &::after {
      content: "";
      background: rgba(0, 0, 0, 0.4);
      height: 100%;
      width: 100%;
      position: absolute;
    }
    &::before {
      content: "";
      bottom: 0;
      left: 50%;
      transform: translateY(50%);
      width: 1px;
      height: 120px;
      position: absolute;
      background: white;
      z-index: 2;
    }
  }
  article.main-part {
    background-color: #eceae8;
    .main-info {
      color: #041e42;
      padding: 75px 0;
      margin-bottom: 120px;
      h2 {
        font-size: 3.125rem;
        font-family: "Griffo M";
        font-weight: 400;
        margin-bottom: 50px;
        position: relative;
        &::after {
          position: absolute;
          bottom: -20px;
          left: 50%;
          transform: translateX(-50%);
          background: #cba052;
          content: "";
          z-index: 1;
          width: 80px;
          height: 3px;
        }
      }
      p {
        font-size: 1.3rem;
        line-height: 1.5;
        &:last-of-type {
          margin-bottom: 3rem;
        }
      }
      a {
        padding: 10px 20px;
        background: #041e42;
        color: white;
        text-transform: uppercase;
        border: 2px solid transparent;
        font-size: 0.875rem;
        &:hover {
          color: #041e42;
          background: transparent;
          border: 2px solid #041e42;
        }
      }
    }
    .main-products {
      margin-bottom: 50px;
      color: #041e42;
      h4 {
        font-family: "Griffo M";
        font-size: 1.875rem;
        text-transform: capitalize;
        position: relative;
        margin-bottom: 5rem;
        &::after {
          position: absolute;
          bottom: -20px;
          left: 50%;
          transform: translateX(-50%);
          background: #cba052;
          content: "";
          z-index: 1;
          width: 80px;
          height: 3px;
        }
      }
      .productItem__imageBox {
        position: relative;
        width: 100%;
        height: 210px;
        overflow: hidden;
        &:hover {
          img {
            transform: scale(1.1);
          }
        }
        img {
          width: 100%;
          height: auto;
        }
      }
      .productItem__info {
        background-color: white;
        padding: 15px;
        h6 {
          text-align: left;
          font-family: "Griffo M";
          padding: 5px 0 40px;
          font-size: 1.75rem;
        }
        a {
          width: 100%;
          background: #041e42;
          color: white;
          text-transform: uppercase;
          font-size: 0.875rem;
          padding: 10px 0;
          letter-spacing: 1px;
          display: block;
          border: 2px solid transparent;
          &:hover {
            border: 2px solid #041e42;
            background: transparent;
            color: #041e42;
          }
        }
      }
    }
  }
}

.usage-area-nav {
  position: -webkit-sticky;
  position: sticky;
  padding-top: 0px;
  top: 200px;
  z-index: 1;

  .wrapper {
    box-shadow: 5px 2px 10px black;
    border-radius: 20px;
    position: absolute;
    left: 80%;
    display: flex;
    flex-direction: column;
    height: auto;
    width: 300px;
    z-index: 10000;
    background-color: #041e42;
    color: white;
    padding: 30px;
    .usage-area-link {
      padding: 5px;
      font-size: 17px;
      color: white;

      &:hover {
        background-color: #fff;
        color: black;
        border-radius: 10px;
      }
    }
  }
}
